:root {
  --active-color: #00acdc;
}

body {
  margin: 0;
}

html {
  text-transform: uppercase;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__item {
  cursor: default;
  justify-content: center;
}

.is-active, .nav__link:hover {
  color: var(--active-color);
}

.header__link {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__item:first-child .nav__link {
  margin-left: 0;
}
